@import url("variables.css");

*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--background-color-light);
}

.default-background {
  background: var(--text-color-light);
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 1100px;
  margin: 0 auto;
  height: 100vh;
}

.title {
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрируем элементы по горизонтали */
  justify-content: center; /* Центрируем элементы по вертикали */
  flex-grow: 1; /* Занимаем оставшееся пространство */
}

.title_inner {
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрируем элементы по горизонтали */
  position: relative; /* Для наложения элементов */
  justify-content: center; /* Центрируем элементы по вертикали */
  flex-grow: 1; /* Занимаем оставшееся пространство */
}

.header {
  padding-top: 32px;
  display: flex;
}

.mobile-navbar {
  display: none;
}

.menu_img {
  display: none;
}

option {
  background-color: none;
}

a {
  text-decoration: none; /* Убирает подчеркивание */
  color: inherit;
}

.menu {
  list-style-type: none;
  padding: 8px 16px;
  color: #000;

  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.23px;
  font-stretch: 107%;
}

.menu:hover {
  letter-spacing: -0.23px;
  color: #646464;
}

.menu_active {
  border-radius: 40px;
  background: var(--Grays-Black, #000);
  color: var(--background-color-light);
}

.navigation {
  margin: 0 auto;
  border-radius: 25px;
  background-color: var(--background-color-light);
  padding: 2px;
  display: flex;
  flex-direction: column; /* По умолчанию вертикальное меню */

  width: auto;
}

.navigation_menu {
  width: 100%;
  height: 100%;
  gap: 8px;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;

  list-style: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
  transition: max-height 1.3s ease;
}

.page-title {
  margin-top: 58px;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-title_islamapp {
  color: var(--text);
  text-align: center;
  font-size: 52px;
  font-style: normal;
  font-weight: 800;
  letter-spacing: 0.37px;
  max-width: 421px;
  font-stretch: 130%;
}

.page-title_text {
  margin-top: 12px;
  color: var(--text);
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.37px;
  font-stretch: 115%;
}

.page-title_nstall {
  margin-top: 40px;
  height: 43px;
  padding: 12px 24px;

  border-radius: 100px;
  background: linear-gradient(135deg, #08e045 0%, #85ed35 57.72%, #c6f62a 100%);
  border: none;
  color: var(--background-color-light);

  text-align: center;
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.page-title_nstall:hover {
  cursor: pointer;
}

.islamApp_conteiner {
  position: relative;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.language {
  align-items: center;
  position: absolute;
  right: 30px;
  outline: none;
  border: none;
  color: white;
  cursor: pointer;
  appearance: none;
  background: url(data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2216%22%20height%3D%2216%22%20viewBox%3D%220%200%2016%2016%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%3Cpath%20d%3D%22M4%206L8%2010L12%206%22%20stroke%3D%22white%22%20stroke-width%3D%221.6%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22/%3E%3C/svg%3E)
    no-repeat right 0px center;
  background-size: 16px;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  word-spacing: 4.07px;
}

.language:focus {
  outline: none; /* Убирает обводку при фокусе и клике */
  border: none;
}

#language {
  display: inline-block;
  width: 46px;
}

.islamApp {
  flex-grow: 1;
  display: flex;
  align-items: flex-end; /* Содержимое внизу */
  justify-content: center; /* Центрирование по горизонтали */
  position: relative; /* Для наложения элементов */
  max-width: 342px;
}

/* Слой с градиентом */
.gradient-background {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 278px;
  height: 389.958px;
  /* Градиент и анимация */
  background: conic-gradient(
    from 0deg,
    #ff5f6d,
    #ffc371,
    #ff5f6d,
    #0072ff,
    #00c6ff,
    #ff5f6d
  );
  background-size: 200% 200%; /* Увеличенный размер для плавного перехода */
  animation: gradientAnimation 8s ease infinite; /* Анимация */
  filter: blur(50px); /* Размытие для свечения */
  overflow: hidden;
  border-radius: 30px;
  z-index: -1;
}

.islamApp_img {
  display: block;
  border-radius: inherit;
  height: 416px;
  width: 100%;
  z-index: 2;
}

.islamApp_img_1 {
  display: none;
  width: 342px;
  z-index: 2;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
  width: 100%;
}

.nav-item:hover {
  color: #646464;
  letter-spacing: -0.23px;
}

.bar_img {
  width: 24px;
  height: 24px;
  margin-top: 0px;
}

span {
  font-size: 12px;
}

.button {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.qr_cod {
  height: 43px;
  padding: 12px 24px;
  margin-bottom: 64px;

  border-radius: 100px;
  background: #fff;
  border: none;
  color: #000;

  text-align: center;
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

/* Ключевые кадры для анимации */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Медиазапрос для мобильных устройств */
@media (max-width: 768px) {
  .conteiner {
    max-width: 575px;
  }

  .menu {
    font-size: 16px;
  }

  .language {
    left: auto;
    top: 12px;
    right: 16px;
  }

  .navigation_menu {
    height: 87px;
  }

  .header {
    justify-content: space-between;
    width: 100%;
  }

  .navigation {
    display: none;
  }

  .islamApp_img {
    display: none;
  }

  .islamApp_img_1 {
    display: block;
  }

  .gradient-background {
    width: 80%;
    height: 98%;
  }

  .page-title {
    margin-top: 37px;
  }

  .page-title_islamapp {
    font-size: 46px;
  }

  .title_islamapp {
    font-size: 31px;
    font-stretch: 127%;
  }

  .page-title_text {
    font-size: 17px;
    margin-top: 8px;
  }

  .title_inner {
    margin-bottom: 52px;
  }

  .page-title_nstall {
    margin-top: 36px;
    margin-bottom: 53px;
    padding: 12px 27px;
  }

  .page-title_islamapp {
    font-size: 32px;
  }

  .mobile-navbar {
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 52px;
    background: rgba(0, 0, 0, 0.42);
    backdrop-filter: blur(35px);
    border-top: 0.5px solid #ccccdd99;
    z-index: 999;
    justify-content: space-around;
    align-items: center;
  }
}

@media (max-width: 568px) {
  .container {
    max-width: 343px;
  }
}

@media (max-width: 380px) {
  .container {
    width: 272px;
  }

  .islamApp {
    width: 272px;
    background-image: url(./images/mockup.png);
    background-repeat: no-repeat;
    height: 346.62px;
  }

  .islamApp_img_1 {
    display: none;
    width: 100%;
    height: 346.62px;
    background-repeat: no-repeat;
  }
}
